.page-content::after {
  content: '';
  clear: both;
  display: block; }

main {
  width: 66.66667%;
  display: block;
  float: left;
  box-sizing: border-box;
  padding: 0 20px; }
  main:first-of-type {
    padding-left: 0; }
  main:last-of-type {
    padding-right: 0; }

.inner {
  max-width: 1450px;
  margin: 0 auto; }

.cf::after {
  content: '';
  clear: both;
  display: block; }

.table .row::after {
  content: '';
  clear: both;
  display: block; }

.table .row.heading {
  font-weight: bold;
  border-bottom: 1px solid #EEEEEE; }

footer.page-footer {
  padding: 2em 0;
  margin-top: 2em;
  border-top: 1px solid #EEEEEE; }

/**
 * Responsive
 **/
@media all and (max-width: 800px) {
  /* navigation */
  .hamburglar {
    display: block; }
  nav.mainnav {
    display: none; }
    nav.mainnav li {
      display: block;
      margin: 0; }
      nav.mainnav li a {
        background: #EEEEEE;
        color: #000000;
        border-bottom: 1px solid #BBBBBB; }
        nav.mainnav li a:hover, nav.mainnav li a.current, nav.mainnav li a.section {
          background: #BBBBBB; }
      nav.mainnav li .menu.level-2 {
        position: relative; }
  section .row .col {
    width: 33.33333%;
    display: block;
    float: left;
    box-sizing: border-box;
    padding: 0 1rem; }
    section .row .col:first-of-type {
      padding-left: 0; }
    section .row .col:last-of-type {
      padding-right: 0; }
  section.grid .item {
    width: 48%;
    box-sizing: border-box;
    margin: 0 2%; }
    section.grid .item:nth-of-type(n), section.grid .item:nth-of-type(1n+1) {
      margin: 4% 2% 0;
      clear: none; }
    section.grid .item:nth-of-type(2n+1) {
      margin-left: 0;
      clear: both; }
    section.grid .item:first-of-type {
      margin-top: 0;
      margin-left: 0; }
    section.grid .item:nth-of-type(1) {
      margin-top: 0; }
    section.grid .item:nth-of-type(2) {
      margin-top: 0; }
    section.grid .item:nth-of-type(2n) {
      margin-right: 0;
      position: relative; }
      section.grid .item:nth-of-type(2n):after {
        clear: both; }
    section.grid .item:last-of-type {
      margin-bottom: 0; } }

@media all and (max-width: 600px) {
  section .row .col {
    width: 100%;
    display: block;
